import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { LoginImagesRoutingModule } from './login-images-routing.module';
import { HttpClientModule } from '@angular/common/http';
import { ReactiveFormsModule } from '@angular/forms';
import { AngularMaterialModule } from '@shared';
import { FragmentsModule } from 'app/modules/fragments/fragments.module';
import { LoginImagesComponent } from './login-images.component';


@NgModule({
  declarations: [LoginImagesComponent],
  imports: [
    CommonModule,
    LoginImagesRoutingModule,
    HttpClientModule,
    ReactiveFormsModule,
    AngularMaterialModule,
    FragmentsModule,
  ]
})
export class LoginImagesModule { }
