import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginImagesComponent } from './login-images.component';

const routes: Routes = [
  {
    path: '',
    component: LoginImagesComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LoginImagesRoutingModule { }
