import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { DomSanitizer } from '@angular/platform-browser';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { LoginImage } from 'app/core/models/login-image.model';
import { DateService } from 'app/core/services/date.service';
import { FileStorageService } from 'app/core/services/file.storage.service';
import { NgxSpinnerService } from 'ngx-spinner';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-login-images',
  templateUrl: './login-images.component.html',
  styleUrls: ['./login-images.component.scss']
})
export class LoginImagesComponent implements OnInit {
  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<LoginImage>;
  displayedColumns: string[] = [
    'originalFilename',
    'fileURL',
    'fileExtension',
    // 'displayed'
  ];

  @ViewChild('docInput') myDocInput: ElementRef;
  @ViewChild('docFile') myDocFile: ElementRef;

  loginImage = new LoginImage();
  loginImageForm: FormGroup;
  loginImageList = [];
  safeURL: any;
  uploadedLoginImage: any;
  fileSize: number;
  selectedFile: File;
  isShowUploadButton = false;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private dateService: DateService,
    private sanitizer: DomSanitizer,
    private spinner: NgxSpinnerService,
    private fileStorageService: FileStorageService,
  ) { }

  ngOnInit(): void {
    this.setFormValidator();
    this.getLoginImageList();
  }

  getLoginImageList(): void {
    this.apiService.findAll(ENDPOINTS.loginImage).subscribe(
      (res: any) => {
        this.loginImageList = res.responseData.data;
        this.dataSource = new MatTableDataSource<LoginImage>(res.responseData.data);
      });

  }

  save(): void {
    Swal.fire({
      text: 'Are you sure you want to upload this image?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        this.fileStorageService.uploadImage(ENDPOINTS.loginImage + `?displayed=true`, this.selectedFile)
          .subscribe((res: any) => {
            this.uploadedLoginImage = res.responseData.data
            this.hideSpinner();
            this.selectedFile = null;
            this.isShowUploadButton = false;
            this.loginImage.originalFilename = null;
            this.sweetAlertService.customSuccessMessage('Image successfully uploaded.');
            this.getLoginImageList();
            this.myDocInput.nativeElement.value = '';
            this.myDocFile.nativeElement.value = '';
            this.loginImage = new LoginImage();
          }, (err: any) => {
            this.hideSpinner();
            this.sweetAlertService.error(err);
          }, () => {
            this.hideSpinner();
          });
      } else {
        this.selectedFile = null;
      }
    });
  }

  viewLoginImageList(): void {
    this.apiService.findById(ENDPOINTS.loginImage, this.formListComponent.idForUpdate).subscribe(
      (res: any) => {
        this.loginImage = res?.responseData?.data;
      }
    )
  }

  viewDocument(obj) {
    window.open(obj.fileURL);
  }

  delete(): void {
    Swal.fire({
      text: 'Are you sure you want to delete this login image?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {

      this.apiService.deleteById(ENDPOINTS.loginImage, this.formListComponent.idForUpdate).subscribe(
        (res: any) => {
          this.myDocInput.nativeElement.value = '';
          this.myDocFile.nativeElement.value = '';
          this.sweetAlertService.customSuccessMessage('Login image successfully deleted.');
          this.getLoginImageList();
          this.loginImage = new LoginImage();
        }
      )
    });
  }

  selectFile(event) {
    if (event.target.files[0]) {
      this.fileSize = event.target.files[0].size;
      const fileURL = URL.createObjectURL(event.target.files[0]);
      this.safeURL = this.sanitizer.bypassSecurityTrustResourceUrl(fileURL);
      const reader = new FileReader();
      const [file] = event.target.files;
      reader.readAsDataURL(file);
      reader.onload = () => {
      };
      this.selectedFile = event.target.files[0];
    }
  }

  compareImageLogin(value: any, selected: any): boolean {
    if (value && selected) {
      return value.id === selected.id;
    }
  }


  setFormValidator(): void {
    this.loginImageForm = this.formBuilder.group({
      originalFileName: [''],
      displayed: [''],
    });
  }

  resetFormValidator(): void {
    this.loginImage = new LoginImage();
  }

  hideSpinner() {
    setTimeout(() => {
      this.spinner.hide();
    }, 1);
  }

}
